 * animation scale-up-center
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


@media screen and (max-width: 375px){
    .section__padding{
        padding: 1rem;
    }
    .section__margin{
        margin: 1rem;
    }
}
@media screen and (max-width: 1920px){
    .section__padding{
        padding: 1rem;
    }
    .section__margin{
